.header__link {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  line-height: 1.22;
  opacity: 1;
  transition: opacity 0.8s linear;
}

.header__link:hover {
  opacity: 0.6;
}

@media screen and (max-width: 424px) {
  .header__link {
    font-size: 14px;
    line-height: 1.21;
  }
}
